
.v-header{
  font-size: 24px;
  height:54px;
  text-align: center;
}
.v-header>small{
  display: block;
  font-weight: 500;
}
.v-question-topic{
padding: 16px 26px;
margin-bottom: 20px;
border: 1px solid #ccc;
position: relative;
}
.question-options li{
  height: 26px;
}
.v-footer{
  height: 130px;
  line-height: 130px;
  text-align: center;
}
.question-data{
  position: absolute;
  height: 100%;
  width: 50%;
  top:0;
  right: 0;
}



